* {
  padding: 0px;
  margin: 0px;
}

@font-face {
  font-family: "Chomsky";
  src: url("Chomsky.otf")
}

body{
  background-color: rgb(136, 136, 136);
}

.container{
  width: 100%;
}

.head-sectn{
  background-color: rgb(0, 0, 0);
  padding: 40px 0 40px 0;
}

.country-head{
  position: absolute;
  top: 0px;
  display: block;
  width: 100%;
}

.country-head select{
  background-color:  #494949;
  border: none;
  color: white;
  float: right;
  text-align: center;
  width: 180px;
  height: 30px;
}

.title-head{
  font-family: 'Chomsky', cursive;
  color: white;
  font-size: 36px;
  text-align: center;
}

.navbar{
  display: flex;
  width: 100%;
  height: 5vh;
  padding: 2px 0 2px 0;
  flex-wrap: wrap;
}

button{
  position: relative;
  flex: 10%;
  height: 100%;
  background-color: #494949;
  border: none;
  outline: solid rgb(122, 122, 122) 2px;
  font-family: 'Ubuntu', serif;
  font-size: 16px;
  font-weight: bold;
  color: white;
}

button:hover{
  color: #ffffff;
  background-color: rgb(122, 122, 122)
}

.cards {
  display: grid;
  padding: 3rem;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
  /* background-color: rgb(193, 243, 228); */
}

.cards-title {
  display: flex;
  flex-direction: row;
  padding: 5vh 5vw 5vh 5vw;
  background-color: rgb(48, 48, 48);
  box-shadow:  0px 0px 10px 5px rgb(131, 131, 131);
}

.cards-title h1{
  font-family: 'Rye', cursive;
  color: white;
  font-size: 32px;
  text-transform: uppercase;
  flex: 50%;
}

.cards-title form input{
  padding-left: 10px;
}

.cards-title form button{
  width: 60px;
  border-radius: 5px;
  cursor: pointer;
  outline: none;
  margin-left: 5px;
  background-color: gray;
}

.cards-title button:hover {
  background: black;
  color: white;
}

.card {
  cursor: pointer;
  background-color: rgb(255,255,255, 0.2);
  padding: 30px;
  border-radius: 30px;
  box-shadow:  0px 0px 10px 2px rgb(80, 79, 79);
  height: 450px;
}

.card-image {
  position: relative;
  height: 100%;
  border-radius: 30px 30px 0px 0px;
}

.card-image img{
  width: 100%;
  height: 100%;
  opacity: 0.8;
  border-radius: 30px 30px 30px 30px;
  transition: transform 0.5s ease;
}

.card-image h1 {
  position: absolute;
  font-size: 20px;
  background-color: rgb(0,0,0,0.7);
  color: white;
  font-family: 'Neucha',cursive;
  padding: 15px;
  right: 0px;
  left: 0px;
  bottom: 30px;
}

.card-image img:hover{
  opacity: 0.87;
  transform: translateY(-2%);
}

.card-image button{
  position: absolute;
  cursor: pointer;
  bottom: 0px;
  left: 0px;
  width:100%;
  height: 30px;
  font-family: 'Neucha',cursive;
  background-color: rgb(255, 255, 255);
  color: black;
  border-radius: 0px 0px 30px 30px;
  outline: none;
}

.card-image button:hover{
  background-color: rgb(0, 0, 0, 0.7);
  color: white;
}

.overlay-card{
  position: absolute;
  top: 0px;
  left: 0px;
  min-height: 100vh;
  background: rgb(0,0,0,0.5);
  font-family: 'Neucha',cursive;
}

.overlay-container{
  display: flex;
  flex-direction: column;
  padding: 30px;
  padding-bottom: 50px;
  height: 550px;
  margin: 10vh 20vw 10vh 20vw;
  background-color: rgb(255,255,255);
  box-shadow:  0px 0px 50px 2px rgb(124, 119, 119);
  font-family: 'Neucha',cursive;
}

.overlay-back{
  margin-left: auto;
  margin-bottom: 10px;
  background: white;
  color: black;
}

.overlay-back button {
   background: transparent;
   cursor: pointer;
   color: black;
   outline: none;
   font-family: 'Neucha',cursive;
   font-size: 18px;
   transition: font-size ease 0.2s;
}

.overlay-back button:hover {
  font-size: 20px;
}

.hzntal{
  display: flex;
  flex-direction: row;
  height: 100%;
}

.vrtcal{
  display: flex;
  flex-direction: column;
  padding: 0px 20px 0px 20px;
  width: 100%;
}

.overlay-source{
  cursor: pointer;
  font-family: 'Neucha',cursive;
  margin-top: auto;
  height: 30px;
}

.overlay-source button{
  width: 100%;
}

.h-image{
  width: 100%;
}

.h-image img{
  width: 100%;
  height: 100%;
}

.vrtcal h1{
  font-size: 3.5vh;
  font-family: 'Merriweather',cursive;
}

#p1{
  font-size: 2.4vh;
  font-weight: bold;
  padding-top: 20px;
  font-family: 'Helvetica',cursive;
}

#p2{
  font-size: 2.4vh;
  font-weight: bold;
  padding-top: 5px;
  font-family: 'Helvetica',cursive;
}

#p3{
  padding: 10px 0px 10px 0px;
  font-size: 3vh;
  font-weight: bold;
  font-family: 'Ubuntu',cursive;
}

@media (max-width: 1000px) {
  button{
    flex: 25%;
    font-size:16px;
  }

  .title-head{
    font-size: 22px;
  }

  .country-head select{
    width: 100px;
    font-size: 8px;
  }

  .cards-title { 
    height: 10vh;
  }

  .cards-title h1{
    font-size: 18px;
    margin: 4vh 3vw 4vh 3vw;
    flex: 50%;
  }

  .cards-title form input{
    width: 80px;
    height: 30px;
    border: none;
    margin: 4vh 0vw 4vh 0vw;
  }

  .cards-title form button{
    width: 30px;
    height: 30px;
    margin: 4vh 3vw 4vh 1vw;
  }

  .hzntal{
    display: flex;
    flex-direction: column;
  }
}

@media (max-width: 1200px){
  .cards {
    display: grid;
    padding: 3rem;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    /* background-color: rgb(193, 243, 228); */
  }

  .hzntal{
    display: flex;
    flex-direction: column;
    height: 100%;
  }

  .vrtcal{
    display: flex;
    flex-direction: column;
    height: 100%;
    padding: 0;
  }

  .overlay-source{
    cursor: pointer;
    font-family: 'Neucha',cursive;
    margin-top: auto;
  }

  .vrtcal h1{
    padding-top: 10px;
    font-size: 2.8vh;
    font-family: 'Merriweather',cursive;
  }
  
  #p1{
    font-size: 2vh;
    font-weight: bold;
    padding-top: 20px;
    font-family: 'Helvetica',cursive;
  }
  
  #p2{
    font-size: 2vh;
    font-weight: bold;
    padding-top: 5px;
    font-family: 'Helvetica',cursive;
  }
  
  #p3{
    padding: 10px 0px 10px 0px;
    font-size: 2.5vh;
    font-weight: bold;
    font-family: 'Ubuntu',cursive;
  }

}

@media (max-width: 800px){
  .cards {
    display: grid;
    padding: 3rem;
    grid-template-columns: repeat(1, 1fr);
    gap: 2rem;
    /* background-color: rgb(193, 243, 228); */
  }
}